
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;
}

article,
section {
  display: block;
}

span {
  font: inherit;
  color: inherit;
}

a {
  color: inherit;
  text-decoration: none;
}

.people {
  display: grid;
  grid-template-columns: 200px 200px;

  grid-gap: 40px 30px;
  justify-content: center;
  align-content: center;
  margin: auto;
}

.person {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  align-items: center;
  border-radius: 10px;
  transition: transform 1s;

  &:hover {
    background-color: #EEEEEE;
    transform: scale(1.2);


  }

  &__photo {
    position: relative;
    margin-bottom: 8px;
  }

  &__rank {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 32px;
    border-radius: 50px;
    color: #8B0000;
    background-color: #fff;
    right: 10px;
    top: calc((220px - 32px) / 2);

  }

  &__image {
    display: flex;
    width: 100%;
    border-radius: 50%;
    
    &:hover {
      border-radius: 60px;
    }
  }


  &__name {
    display: block;
    font-size: 20px;
    font-weight: 700;
    color: #2F4F4F;
    margin-bottom: 9px;
    text-align: center;
  }
}
